import React,{useState,useEffect} from 'react'
import {Button,Row,Col,ListGroup,Image,Card} from 'react-bootstrap'
import {Link} from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'
import Message from '../Components/Message'
import Loader from '../Components/Loader'
import { getOrderDetail, payOrder, deliverOrder } from '../Actions/orderaction'
import {PayPalButton} from 'react-paypal-button-v2'
import {ORDER_PAY_RESET, ORDER_DELIVER_RESET} from '../constants/orderConst'

const OrderScreen = ({match,history}) => {
    const orderId = match.params.id 
    const dispatch = useDispatch()
    const [sdkReady,setSdkReady] = useState(false)

    const orderDetails = useSelector(state=>state.orderDetails)
    const {order, error, loading} = orderDetails

    const orderPay = useSelector(state=>state.orderPay)
    const {loading:loadingPay, success:successpPay} = orderPay

    const orderDeliver = useSelector(state=>state.orderDeliver)
    const {loading:loadingDeliver, success:successpDeliver} = orderDeliver

    const userLogin = useSelector(state=>state.userLogin)
    const {userInfo} = userLogin


    if(!loading && !error){
        order.itemsPrice = order.orderItems.reduce((acc,item)=> acc+ item.price * item.qty, 0).toFixed(2)
    }
  

    const AddPayPalScript= () =>{
        const Script = document.createElement('script')
        Script.type = 'text/javascript'
        Script.src = `https://www.paypal.com/sdk/js?client-id=AZqE6Ijs62yuMNKH_O-6CpRekYrPhieH7lFk_po5vJ2FqP4MAaxctX9tH1ZsgZETOE5z43w359EfUuDb`
        Script.async = true
        Script.onload = () =>{
            setSdkReady(true)
        }
        document.body.appendChild(Script)
    }

    useEffect(()=>{
        if(!userInfo){
            history.psu('/login')
        }

        if(!order || successpPay || order._id !== Number(orderId) || successpDeliver){
            dispatch({type:ORDER_PAY_RESET})
            dispatch({type:ORDER_DELIVER_RESET})
            dispatch(getOrderDetail(orderId))
        }else if(!order.isPaid){
            if(!window.paypal){
                AddPayPalScript()
            }else{
                setSdkReady(true)
            }
        }
    
    },[dispatch,order,orderId,successpPay,successpDeliver])

    const successPayHandler = (paymentResult)=>{
        dispatch(payOrder(orderId,paymentResult))
    }
    const deliverHandler = () =>{
        dispatch(deliverOrder(order))
    }
    return loading ? (<Loader></Loader>) : error ? (<Message variant='danger'>{error}</Message>) 
    :(
        <div>
            <h1>Order: {order._id}</h1>
            <Row>
                <Col md={8}>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Shipping</h2>
                            <p>
                            <strong>Name:</strong>{order.user.name}
                            </p>
                            <p>
                            <strong>Email:</strong><a href={`mailto:${order.user.email}`}>{order.user.email}</a>
                            </p>
                          
                            <p>
                                <strong>shipping: </strong>
                                {order.shippingAddress.address}, {order.shippingAddress.city},
                                {' '}
                                {order.shippingAddress.Postalcode},
                                {' '}
                                {order.shippingAddress.country}
                            </p>
                            {order.isDelivered ? (
                                <Message variant='success'>Delivered on {order.deliveredAt}</Message>
                            ):(
                                <Message variant='warning'>Not Deliveredat</Message>
                            )}
                        </ListGroup.Item>

                        <ListGroup.Item>
                        <h2>Payment Method</h2>
                        <p>
                            <strong>Method: </strong>
                            {order.paymentMethod}
                        </p>
                        {order.isPaid ? (
                            <Message variant='success'>Paid on {order.paidAt}</Message>
                        ):(
                            <Message variant='warning'>Not paid</Message>
                        )}
                    </ListGroup.Item>

                    <ListGroup.Item>
                        <h2>Order Items</h2>
                        {order.orderItems.length === 0 ? <Message variant='info'>Order is empty</Message>:
                    (
                        <ListGroup variant='flush'>
                        {order.orderItems.map((item,index)=>(
                            <ListGroup.Item key={index}>
                                <Row>
                                    <Col md={1}>
                                        <Image src={item.image} alt={item.name} fluid rounded></Image>
                                    </Col>
                                    <Col>
                                        <Link to={`/product/${item.product}`}>{item.name}</Link>
                                    </Col>
                                    <Col md={4}>
                                        {item.qty} X ${item.price} = ${(item.qty * item.price).toFixed(2)}
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                        </ListGroup>
                    )}
                    </ListGroup.Item>
                    </ListGroup>
                </Col>
                <Col md={4}>
                    <Card>
                        <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <h2>order Summary</h2>
                            </ListGroup.Item>

                            <ListGroup.Item>
                             <Row>
                                <Col>Item:</Col>
                                <Col>${order.itemsPrice}</Col>
                             </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                        <Row>
                           <Col>Shipping:</Col>
                           <Col>${order.shippingPrice}</Col>
                        </Row>
                   </ListGroup.Item>

                   <ListGroup.Item>
                   <Row>
                      <Col>Tax:</Col>
                      <Col>${order.taxPrice}</Col>
                   </Row>
              </ListGroup.Item>

              <ListGroup.Item>
              <Row>
                 <Col>Total:</Col>
                 <Col>${order.totalPrice}</Col>
              </Row>
         </ListGroup.Item>


         {!order.isPaid && (
             <ListGroup.Item>
              {loadingPay && <Loader></Loader>}

              {!sdkReady ? (
                  <Loader/>
              ):(
                <PayPalButton
                amount={order.totalPrice}
                onSuccess={successPayHandler}
                ></PayPalButton>
              )}
             </ListGroup.Item>
         )
         }
               
                        </ListGroup>
                        {loadingDeliver && <Loader></Loader>}

                        {
                            userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered && (
                                <ListGroup.Item>
                                    <Button
                                        type='button'
                                        className='btn btn-block'
                                        onClick={deliverHandler}
                                    >
                                        Mark as Deliver
                                    </Button>
                                </ListGroup.Item>
                            )
                        }
                    </Card>
                </Col>
            </Row>
        </div>
    )
}

export default OrderScreen
