import React,{useState,useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {Table,Button,Row,Col} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import Loader from '../Components/Loader'
import Paginate from '../Components/Paginate'
import Message from '../Components/Message'
import { listProducts, deleteProduct,CreateProduct } from '../Actions/productaction'
import { PRODUCT_CREATE_RESET } from '../constants/productconstant'


const ProductListScreen = ({history,match}) => {
    const dispatch = useDispatch()

    const productList = useSelector(state => state.productList)
    const {error, loading, products,pages,page} = productList

    const productDelete = useSelector(state => state.productDelete)
    const {error:errorDelete, loading:loadingDelete, success:successDelete} = productDelete

    const productCreate = useSelector(state => state.productCreate)
    const {error:errorCreate, loading:loadingCreate, success:successCreate, product:createProduct} = productCreate

    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin

    let keyword = history.location.search
    useEffect(()=>{
        dispatch({type: PRODUCT_CREATE_RESET})

        if(!userInfo.isAdmin){
            history.push('/login')
        }

        if(successCreate){
            history.push(`/admin/product/${createProduct._id}/edit`)
        }else{
            dispatch(listProducts(keyword))
        }
      
    },[dispatch,history, userInfo,successCreate,createProduct,successDelete,keyword])

    const deletehandler = (id) =>{
        if(window.confirm('are you sure you want to delete this user')){
            dispatch(deleteProduct(id))
        }

    }

    const createproducthandelr = ()=>{
        dispatch(CreateProduct())
    }

    return (
        <div>
            <Row className='align-items-center'>
                <Col>
                    <h1>Products</h1>
                </Col>
                <Col className='text-right'>
                    <Button className='my-3' onClick={createproducthandelr}>
                        <i className='fas fa-plus'></i> create Product
                    </Button>
                </Col>
            </Row>
            {loadingDelete && <Loader/>}
            {errorDelete && <Message variant='danger'>{errorDelete}</Message>}
            {loadingCreate && <Loader/>}
            {errorCreate && <Message variant='danger'>{errorCreate}</Message>}
            {loading
            ? <Loader></Loader> 
            : error 
            ? (<Message variant='danger'>{error}</Message>)
            :(
                <div>
               <Table striped bordered hover responsive className='table-sm'>
               <thead>
               <tr>
                <th>ID</th>
                <th>NAME</th>
                <th>PRICE</th>
                <th>CATEGORY</th>
                <th>BRAND</th>
                <th></th>
            </tr>
               </thead>
               <tbody>
                {
                    products.map(product=>(
                        <tr key={product._id}>
                        <td>{product._id}</td>
                        <td>{product.name}</td>
                        <td>${product.price}</td>
                        <td>{product.category}</td>
                        <td>{product.brand}</td>
                        <td>
                            <LinkContainer to={`/admin/product/${product._id}/edit`}>
                                <Button variant='info' className='btn-sm'>
                                <i className='fas fa-edit'></i>Edit
                                </Button>
                            </LinkContainer>

                            <Button variant='danger' className='btn-sm' onClick={()=>deletehandler(product._id)}>
                            <i className='fas fa-trash'></i>Delete
                            </Button>
                        </td>
                        </tr>
                    ))
                }
               </tbody>
               </Table>
               <Paginate pages={pages} page={page} isAdmin={true}></Paginate>
               </div>
            )}
        </div>
    )
}

export default ProductListScreen
