import React,{useState,useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {Table,Button} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import Loader from '../Components/Loader'
import Message from '../Components/Message'
import { listOrder } from '../Actions/orderaction'


const OrderListScreen = ({history}) => {
    const dispatch = useDispatch()

    const orderList = useSelector(state => state.orderList)
    const {error, loading, orders} = orderList
    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin


    useEffect(()=>{
        if(userInfo && userInfo.isAdmin){
            dispatch(listOrder())
        }else{
            history.push('/login')
        }
      
    },[dispatch,history, userInfo])



    return (
        <div>
            <h1>Orders</h1>
            {loading
            ? <Loader></Loader> 
            : error 
            ? (<Message variant='danger'>{error}</Message>)
            :(
               <Table striped bordered hover responsive className='table-sm'>
               <thead>
               <tr>
                <th>ID</th>
                <th>USER</th>
                <th>DATE</th>
                <th>Price</th>
                <th>PAID</th>
                <th>Delivered</th>
                <th></th>
            </tr>
               </thead>
               <tbody>
                {
                    orders.map(orders=>(
                        <tr key={orders._id}>
                        <td>{orders._id}</td>
                        <td>{orders.user && orders.user.name}</td>
                        <td>{orders.createdAt.substring(0,10)}</td>
                        <td>${orders.totalPrice}</td>
                        <td>{orders.isPaid ? (
                           orders.paidAt.substring(0,10)
                        ):(
                            <i className='fas fa-check' style={{color:'red'}}></i>
                        )}</td>

                        <td>{orders.isDelivered ? (
                            orders.deliveredAt.substring(0,10)
                         ):(
                             <i className='fas fa-check' style={{color:'red'}}></i>
                         )}</td>
                        <td>
                            <LinkContainer to={`/order/${orders._id}/edit`}>
                                <Button variant='info' className='btn-sm'>
                                    Details
                                </Button>
                            </LinkContainer>

                        </td>
                        </tr>
                    ))
                }
               </tbody>
               </Table>
            )}
        </div>
    )
}

export default OrderListScreen
