import React,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import {Row,Col,Image,ListGroup,Button,Card, Form} from 'react-bootstrap'
import Review from '../Components/Rating'
import {useDispatch, useSelector} from 'react-redux'
import { listProductDetail, CreateProductReview } from '../Actions/productaction'
import Loader from '../Components/Loader'
import Message from '../Components/Message'
import {PRODUCT_CREATE_REVIEW_RESET} from '../constants/productconstant'
import Rating from '../Components/Rating'


function Productscreen({match, history}) {
    const [qty, setQty] = useState(1)
    const [rating, setrating] = useState(0)
    const [comment, setcomment] = useState('')

    const dispatch = useDispatch()



    const productDetails = useSelector(state => state.productDetail)
    const {loading, error, product} = productDetails


    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin
 
    const productReviewCreate = useSelector(state => state.productReviewCreate)
    const {loading:loadingproductReview, error:errorproductReview, success:successproductReview} = productReviewCreate

    useEffect(()=>{
        if(successproductReview){
            setrating(0)
            setcomment('')
            dispatch({type:PRODUCT_CREATE_REVIEW_RESET})
        }
        dispatch(listProductDetail(match.params.id))
    }, [dispatch,match,successproductReview])

    const addToCartHandler = () =>{
        history.push(`/cart/${match.params.id}?qty=${qty}`)
    }

    const submithandler = (e) =>{
        e.preventDefault()
        dispatch(CreateProductReview(
            match.params.id,{
                rating,
                comment
            }
       
        ))
    }

    return (
        <div>
            <Link to='/' className='btn btn-light my-3'>Go Back</Link>
            {loading ? <Loader/>
            : error ? <Message variant='danger'>{error}</Message>
                :(
                    <div>
                    <Row>
                
                <Col md={6}>
                    <Image src={product.image} alt={product.name} fluid/>
                </Col>


                <Col md={3}>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h3>{product.name}</h3>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Review value={product.rating} text={`${product.numReviews} reviews`} color={'#f8e825'}/>
                        </ListGroup.Item>

                        <ListGroup.Item>
                        Price: ${product.price}
                        </ListGroup.Item>

                        <ListGroup.Item>
                        Description: <br></br> {product.description}
                        </ListGroup.Item>
                    </ListGroup>
                </Col>

                <Col md={3}>
                    <Card>
                        <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Price:</Col>
                                    <Col>
                                    <strong>${product.price}</strong>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                            <Row>
                                <Col>Status:</Col>
                                <Col>
                                    {product.countInStock > 0 ? <span style={{color:'green'}}>In Stock</span> : <span style={{color:'red'}}>Out of Stock</span>}
                                </Col>
                            </Row>
                        </ListGroup.Item>
                        {product.countInStock > 0 &&(
                            <ListGroup.Item>
                            <Row>
                                <Col>Qty: </Col>
                                <Col xs='auto' className='my-1'>
                                    <Form.Control
                                        as="select"
                                        value={qty}
                                        onChange={(e)=>setQty(e.target.value)}
                                    >
                                        {
                                            
                                            [...Array(product.countInStock).keys()].map((x)=>
                                            <option key={x+1} value={x+1}>
                                                {x+1}
                                            </option>
                                            )
                                        }
                                    
                                    </Form.Control>
                                </Col>
                            </Row>
                            </ListGroup.Item>
                        )}

                            <ListGroup.Item className='d-flex justify-content-center'>
                                <Button onClick={addToCartHandler} variant={product.countInStock === 0 ? 'danger' : 'success'} className='btn-block' disabled={product.countInStock === 0 ? true : false} type='button'>{product.countInStock === 0 ? 'Unavailable' : 'Add to Cart'}</Button>
                            </ListGroup.Item>
                        </ListGroup>
                    </Card>
                </Col>
            </Row>
            <Row>
                <Col md={6}>
                 <h4>Reviews</h4>
                 {product.reviews.length === 0 && <Message variant='info'>No Review</Message>}
                <ListGroup variant='flush'>
                            {product.reviews.map(review=>(
                                <ListGroup.Item key={review.id}>
                                    <strong>{review.name}</strong>
                                    <Rating value={review.rating} color='#f8e825'></Rating>
                                    <p>{review.createdAt.substring(0, 10)}</p>
                                    <p>{review.comment}</p>
                                </ListGroup.Item>
                            ))}

                            <ListGroup.Item>
                                <h4>Write a Review</h4>

                                {loadingproductReview && <Loader></Loader>}
                                {successproductReview && <Message variant='success'>Review Submitted</Message>}
                                {errorproductReview && <Message variant='danger'>{errorproductReview}</Message>}
                                {userInfo ? (
                                    <Form onSubmit={submithandler}>
                                        <Form.Group controlId='rating'>
                                            <Form.Label>Rating</Form.Label>
                                            <Form.Control
                                                as='select'
                                                value={rating}
                                                onChange={e=> setrating(e.target.value)}
                                            >
                                                <option value=''>Select...</option>
                                                <option value='1'>1 - Poor</option>
                                                <option value='2'>2 - Fair</option>
                                                <option value='3'>3 - Good</option>
                                                <option value='4'>4 - Very good</option>
                                                <option value='5'>5 - Excellent</option>
                                            </Form.Control>
                                        </Form.Group>

                                        <Form.Group controlId='comment'>
                                            <Form.Label>Review</Form.Label>
                                            <Form.Control as='textarea' row='5' value={comment} onChange={e=>setcomment(e.target.value)}>


                                            </Form.Control>
                                        </Form.Group>


                                                
                                        <Button
                                        disabled={loadingproductReview}
                                        type='submit'
                                        variant='primary'
                                    >
                                    Submit
                                    </Button>

                                    </Form>
                                ):( 
                                    <Message variant='info'>Please <Link to='/login'>Login</Link> to write a review </Message>
                                )}
                            </ListGroup.Item>
                </ListGroup>
                 </Col>
            </Row>        
            
            </div>
            )
        }
        </div>
    )
}

export default Productscreen
